header {
    width: 100%;
    text-transform: capitalize;
}
.header__logo {
    background: url('../../assets/Logo@1X.png') no-repeat;
    height: 30px;
    width: 69px;
    margin: auto;
}
.header__hamburger {
    width: 35px;
    margin: 20px auto;
}
.header__hamburger_stick {
    border: 1px solid rgb(65, 65, 65);;
    margin: 5px 0;
    height: 4px;
    background: rgb(65, 65, 65);
    border-radius: 3px;
}
.header__menu {
    height: 0px;
    overflow: hidden;
    background: rgb(29, 42, 114);
    font-size: 1.4rem;
    color: white;
    transition: all 0.5s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.header__menu div:hover, .header__menu-global div:hover, .header__contuct-us {
    cursor: pointer;
}
.header__language, .header__contuct-us, .header__menu-global {
    display: none
}


@media (min-width: 768px) {
    header {
        display: flex;
        margin-top: 77px;
        justify-content: space-around;
        margin-bottom: 100px;

    }
    .header__logo {
        margin: 0px;
    }
    .header__menu-global {
        display: flex;
        width: 550px;
        height: 17px;
        justify-content: space-between;
        font-size: 17px;
        color: #314584;
        font-weight: bold;
        font-family: sans-serif;
        margin: 0px;
    }
    
    .header__hamburger, .header__menu {
        display: none;
    }
    .header__language {
        display: initial;
        margin: 0px;
        border: none;
        text-transform: uppercase;
        font-size: 16px;
        color: #314584;
        font-weight: bold;
        font-family: sans-serif;
    }
    .header__contuct-us {
        display: initial;
        width: 200px;
        height: 46px;
        margin: 0px;
        color: #FF3946;
        line-height: 26px;
        font-size: 20px;
        font-weight: bold;
        box-shadow: 0 0 10px 0 gray;
    }
}
@media (min-width: 1920px) {
    header {
        justify-content: unset;
    }
    .header__logo {
        margin: 0 0 0 123px;
    }
    .header__menu-global {
        margin: 8px 0 0 179px;
    }
    .header__language {
        margin: 2px 0 30px 331px;
    }
    .header__contuct-us {
        margin: 2px 0 10px 22px;
    }
}