main {
    width: 100%;
    text-transform: capitalize;
    background-color: #5b80fa23;
}
.main__portfolio {
    width: calc(100% - 60px);
    margin: 0 0 0 30px;
    text-align: left;
}
.main__portfolio_header {
    font-size: 52px;
    line-height: 62px;
    font-weight: bold;
    color: #314584;
    text-transform: initial;
    margin-bottom: 75px;
}
.main__portfolio_content-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.main__portfolio_content-wrapper div {
    height: 281px;
    width: 371px;
    background-color: #C3D0D8;
    margin-bottom: 30px;
}
.main__portfolio_more-info {
    color: #314584;
    font-size: 19px;
    font-weight: bold;
    margin: auto;
    text-transform: initial;
}
.main__portfolio_more-info:hover {
    cursor: pointer;
}
@media (min-width: 768px) {
    main {
        background-color: #ffffff;
    }
    .main__portfolio {
        width: calc(100% - 200px);
        margin: 0 0 0 100px;
        text-align: left;
    }
    .main__portfolio_header {
        font-size: 52px;
        line-height: 62px;
        font-weight: bold;
        color: #314584;
        text-transform: initial;
        margin-bottom: 75px;
    }
    .main__portfolio_content-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .main__portfolio_content-wrapper div {
        height: 281px;
        width: 371px;
        background-color: #C3D0D8;
        margin-bottom: 30px;
    }
    .main__portfolio_more-info {
        color: #314584;
        font-size: 19px;
        font-weight: bold;
        margin: auto;
        text-transform: initial;
    }
}
@media (min-width: 1920px) {
    .main__portfolio {
        width: 1169px;
        height: 845px;
        margin: 155px 0 0 368px;
    }
    .main__portfolio_header {
        margin-bottom: 75px;
    }
    .main__portfolio_more-info {
        margin: 20px 0 0 500px;
    }
}







.main__contacts {
    width: calc(100% - 60px);
    background-color: #5796af2f;
    padding: 0 30px;
    height: 500px;
    text-align: left;

}
.main__contacts_link {
    color: red;
    padding-top: 8px;
    text-transform: uppercase;
    margin: 8px 0 0 10px;
    font-size: 19px;
}
.main__contacts_header {
    font-size: 31px;
    color:#314584;
    font-weight: bold;
    margin: 8px 0 0 10px;
}
.data_email {
    border: 0px;
    background-color: rgb(218, 235, 245);
    color: #142664;
    font-weight: bold;
    font-size: 20px;
    margin: 43px 0 0 20px;
    width: 40%;
    height: 53px;
}
.data_subject {
    border: 0px;
    background-color: rgb(218, 235, 245);
    color: #142664;
    font-weight: bold;
    font-size: 20px;
    margin: 43px 0 0 13px;
    width: 40%;
    height: 53px;
}
.main__contacts_message {
    border: 0px;
    background-color: rgb(218, 235, 245);
    color: #142664;
    font-weight: bold;
    font-size: 20px;
    margin: 43px 0 0 20px;
    width: calc(80% + 13px);
    height: 53px;
}
.main__contacts_send {
    background-color: red;
    width: 160px;
    height: 50px;
    line-height: 50px;
    padding-left: 40px;
    border-radius: 50px;
    color: white;
    margin: 30px auto;;
    font-weight: bold;
    font-size: 18px;
}
.main__contacts_send:hover {
    cursor: pointer;
}


@media (min-width: 768px) {
    .main__contacts {
        background-color: white;
        width: calc(100% - 200px);
        padding: 0 100px;
    }
    .data_subject {
        margin: 43px 0 0 63px;
    }
    .main__contacts_message {
        width: calc(80% + 63px);

    }
    .main__contacts_header {
        font-size: 51px;

    }
}
@media (min-width: 1920px) {
    .main__contacts {
        width: 1500px;
        height: 500px;
        margin: 304px 0 0 364px;
        text-align: left;
        padding: 0px;
    }
    .data_email {
        width: 551px;
    }
    .data_subject {
        width: 551px;
    }
    .main__contacts_message {
        width: 1162px;
    }
    .main__contacts_send {
        margin: 130px 0 0 490px;
    }
}