.main__slider_left, .main__slider_right {
    display: none;
}
.slider-content_case0 .slider-content_image {
    background:url('../../../assets/Illustration@1X.png') no-repeat;
    width: 100%;
    height: 460px;
    background-size: 100%;
}
.text_header {
    font-size: 47px;
    color: #314584;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    text-transform: initial;
}
.text_description {
    margin-top: 37px;
    font-size: 20px;
}
.text_additional-info {
    display: flex;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    justify-content: center
}
.text_additional-info_more-about {
    color: white;
    line-height: 56px;
    padding-left: 0px;
    background-color: #FF3946;
    width: 160px;
    height: 56px;
    border-radius: 100px;
    font-size: 19px;
}
.text_additional-info_more-about:hover {
    cursor: pointer;
}
.text_additional-info_get-in-touch {
    height: 56px;
    line-height: 56px;
    padding-left: 10px;
    font-size: 21px;
    color: #314584;
}
.text_additional-info_get-in-touch {
    cursor: pointer;
}
.main__slider {
    background-color: #15339623;
}
.slider-content_case1, .slider-content_case2 {
    display: none;
}
@media (min-width: 768px) {
    .main__slider {
        background-color: rgb(255, 255, 255);
    }
    .main__slider_left, .main__slider_right,.slider-content_case1, .slider-content_case2 {
        display: block;
    }
    .main__slider {
        display: flex
    }
    .main__slider_content {
        width: 100%;
    }
    .slider-content_case {
        position: absolute;
        z-index: 0;
        width: calc(100% - 120px);
        background: white;
        transition: all 0.7s ease-in-out;
        display: flex;
    }
    .slider-content_case0 .slider-content_image {
        background:url('../../../assets/Illustration@1X.png') no-repeat;
        width: 680px;
        height: 660px;
        background-size: 100%;
    }
    .slider-content_case1 .slider-content_image {
        background:url('../../../assets/lorem0.png') no-repeat;
        width: 680px;
        height: 660px;
        background-size: 100%;
    }
    .slider-content_case2 .slider-content_image {
        background:url('../../../assets/lorem1.png') no-repeat;
        width: 680px;
        height: 660px;
        background-size: 100%;
    }
    .slider-content_case0 {
        z-index: 1;
    }
    .slider-content_text {
        display: flex;
        flex-direction: column;
        width: 566px;
        margin: 0px 0 0 48px;
        text-align: left;
    }
    .text_header {
        font-size: 77px;
    }
    .text_description {
        margin-top: 37px;
        font-size: 20px;
    }
    .text_additional-info {
        margin-top: 40px;
        margin-left: 7px;
        margin-bottom: 0px;
        display: flex;
        justify-content: unset;
    }
    .text_additional-info_more-about {
        color: white;
        line-height: 56px;
        padding-left: 40px;
        background-color: #FF3946;
        width: 160px;
        height: 56px;
        border-radius: 100px;
        font-size: 19px;
    }
    .text_additional-info_get-in-touch {
        height: 56px;
        line-height: 56px;
        padding-left: 40px;
        font-size: 21px;
        color: #314584;
    }
    .slider-content_image {
        margin-left: 51px;
    }
    .slider-content_case1 .slider-content_text {
        margin: 0px 0 0 48px;
    }
    .slider-content_case2 .slider-content_text {
        margin: 0px 0 0 48px;
    }
    .main__slider_left, .main__slider_right {
        height: 680px;
        width: 60px;
        background-color: rgb(194, 195, 255);
        box-shadow: 0 0 20px 10px white inset;
        transition: all 0.5s ease-in-out;
    }
    .main__slider_left:hover, .main__slider_right:hover {
        background-color: rgb(107, 108, 163);
        cursor: pointer;
    }
    .main__slider_left:active, .main__slider_right:active {
        background-color: rgb(104, 107, 255);
    }
}

@media (min-width: 1920px) {   
    .slider-content_text {
        margin: 147px 0 0 308px;
    }
    .text_description {
        margin-top: 37px;
    }
    .text_additional-info {
        margin-top: 40px;
        margin-left: 7px;
    }
    .slider-content_image {
        margin-left: 181px;
    }
    .slider-content_case1 .slider-content_text {
        margin: 0px 0 0 308px;
    }
    .slider-content_case2 .slider-content_text {
        margin: 100px 0 0 308px;
    }
}